.support{
  gap: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 1em;
}
.support section{
  display: flex;
  flex-direction: column;
  width: 100%;
}
.support section button{
  width: 100%;
  border: 0px solid transparent;
  border-radius: 8px;
  background-color: #5d1917;
  color: white;
  font-size: 30px;
  font-weight: bold;
  font-family: 'Allura';
  padding: 0.5em 1em;
  margin-top: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.6);
}